import React from 'react'
import './RoboticDiscountPage.css'
import { useState } from 'react'
import AOS from 'aos';
import 'aos/dist/aos.css';

function RoboticDiscountPage() {
  const [selected, setSelected] = useState(null)
  const toggle = (i) => {
    if (selected === i) {
      return setSelected(null)
    }

    setSelected(i)
  }
  return (
    <section id='robotics' className="robotics-container container" data-aos='slide-left' data-aos-delay="500">

      <h2><p className='first-paragraph'>Ulga na </p><p>robotyzacj</p></h2>
      <h3>Ulga na robotyzacj 2022</h3>
          <div className='faq-wrapper container'>
            <div className='accordion'>
              {data.map((item, i) => (
                <div className='item'>
                  <div className='title' onClick={() => toggle(i)}>
                    <h4>{item.question}</h4>
                    <span>{selected=== i ? '-' : '+'  }</span>
                  </div>
                  <div className={selected=== i ? ' content show' : 'content'  }>{item.answer}</div>
                </div>
              ))}
            </div>
          </div>
    </section>
  )
}

